import uniPopup from "@/components/uni-popup/uni-popup.vue"

export default {
	components: {uniPopup},
	data() {
		return {
			service:'',
			popup: 0,
		}
	},
	methods: {
		servicePhone(phone){
			uni.makePhoneCall({
			    phoneNumber: phone,
			});
		},
		downPic() {
			uni.downloadFile({
				url: this.service.logo,
				success: (res) =>{
					if (res.statusCode === 200){
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									title: "保存成功",
									icon: "none"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						});
					}
				}
			})
			this.popup = 0
		},
		openPopup() {
			this.popup = 1
		},
		canceldown() {
			this.popup = 0
		}
	},
	onLoad() {
		this.request.httpRequest('api/service/index','GET').then(res =>{
			this.service = res.data.retval
		})
	}
}
